html {font-size: 100px;}
@media (min-width: 480px) {
  html {font-size: 110px !important;}}
@media (min-width: 640px) {
  html {font-size: 120px !important;}}
@media (min-width: 720px) {
  html {font-size: 130px !important;}}

.commodity-wrap{
  background-color: #f3f4f6;
  .loading {
    margin: 0.2rem 0;
  }
}
.my-order{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 .1rem 0 .48rem;
  height: .48rem;
  line-height: .48rem;
  font-size: .15rem;
  color: #000;

  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
  background: #fff url(http://timg.ffan.com/convert/resize/url_T11lCTB5hy1RCvBVdK/tfs/1.png) no-repeat .1rem center;
  background-size: .28rem auto;
  .icon-into{
    display: inline-block;
    width: .08rem;
    height: .14rem;
    background: url(http://timg.ffan.com/convert/resize/url_T13rWTBm_v1RCvBVdK/tfs/1.png) no-repeat;
    background-size: 100% 100%;
  }
}


