@import "../../Elements/base.scss";

.preview-ele{
  .basic-info-s2{
    position: relative;
    height: $editor-preview-base-height * 2.9;
    overflow: hidden;
    .bg{
      height: $editor-preview-base-height * 1.7;
    }
    .contact-s{
      padding: 4px 10px;
      font-size: 12px;
      color: #666;
    }
    .site,.contact-tel{
      display: inline-block;
      margin-right: 4px;
      width: 9px;
      vertical-align: middle;
    }
    .site-text{
      width: 90%;
      @include text-overflow;
      vertical-align: middle;
    }
  }
  .layout-left{
    .logo-img-s{
      position: absolute;
      top: $editor-preview-base-height + 16px;
      left: 10px;
      width: 70px;
      height: 70px;
    }
    .name-s{
      padding-left: 86px;
      width: 288px;
      font-size: 16px;
      color: #000;
      @include text-overflow;
    }
    .contact-s{
      padding-top: 12px;
    }
  }
  .layout-right{
    .logo-img-s{
      position: absolute;
      top: $editor-preview-base-height - 4px;
      left: 50%;
      margin-left: -38px;
      width: 70px;
      height: 70px;
    }
    .name-s{
      margin: 14px auto 0;
      width: 276px;
      font-size: 16px;
      color: #000;
      text-align: center;
      @include text-overflow;
      display: block;
    }
  }
}


