@import "../../Elements/base.scss";

$logo-img-width: 48px;

.preview-ele{
  img{
    display: block;
    width: 100%;
  }
  .basic-info{
    position: relative;
    height: $editor-preview-base-height * 2;
    overflow: hidden;
    .logo {
      width: 100%;
      position: absolute;
      left: 0;
      top: 0;
      .logo-img{
        display: inline-block;
      }
      .name {
        font-weight: bold;
        color: #fff;
        @include text-overflow;
      }
    }
    .contact {
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 0 10px;
      height: 34px;
      background: rgba(255, 255, 255, 0.3);
      @include flexCenterBetween;
      .address {
        padding-right: 5px;
        width: $editor-c-preview-width - 48px;
        color: #fff;
        display: flex;
        align-items: center;
        .site {
          display: inline-block;
          margin-right: 5px;
          width: 8px;
          height: 12px;
        }
        .site-text {
          font-size: 12px;
          width: 230px;
          // max-height: 28px;
          // line-height: 14px;
          // overflow: hidden;
          @include text-overflow;
          text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
        }
      }
      .contact-phone {
        padding: 6px 0 6px 10px;
        border-left: 1px solid rgba(255, 255, 255, 0.3);
        .contact-tel{
          width: 16px;
          height: 16px;
        }
      }
    }
  }
  .layout-left{
    .logo{
      position: absolute;
      // top: $editor-preview-base-height + 16px;
      // left: 10px;
      top: 0;
      left: 0;
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
    }
    .name{
      padding-left: 86px;
      width: 288px;
      font-size: 16px;
      color: #000;
      @include text-overflow;
    }
  }
  .layout-right{
    .logo{
      position: absolute;
      top: 0;
      left: 50%;
      margin-left: -100px;
      width: 200px;
    }
    .name{
      width: 276px;
      font-size: 16px;
      color: #000;
      text-align: center;
      @include text-overflow;
      display: block;
    }
  }
}
.ele {
    overflow: hidden;
    .detail-title {
        @include detailTitle;
    }
    .item {
        display: inline-block;
        .item-img {
            width: 83px;
            height: 42px;
            cursor: pointer;
        }
        .row-radio {
            position: relative;
            margin: 6px 0;
            cursor: pointer;
            font-size: 12px;
            color: $radio-color;
            .radio-btn {
                margin-right: 2px;
                vertical-align: -3px;
            }
        }
        .radio-input {
            position: absolute;
            top: 2px;
            left: -3px;
            width: 16px;
            height: 15px;
            opacity: 0;
            cursor: pointer;
            z-index: 1;
        }
        @include rowRadio($radio-color);
    }
    .item:first-child {
        margin-right: 14px;
    }
}

@media screen and (min-height : 601px) {
  .preview-ele {
    .layout-left {
        .logo {
          padding: 35px 16px 0;
          .logo-img {
            width: $logo-img-width;
            height: $logo-img-width;
            vertical-align: middle;
          }
          .name {
            padding-left: 6px;
            width: $editor-c-preview-width - $logo-img-width - 40px;
            font-size: 14px;
            vertical-align: middle;
          }
        }
    }
    .layout-right{
        .logo {
          padding: 27px 16px 0;
          text-align: center;
          .logo-img {
            width: 44px;
            height: 44px;
          }
          .name {
            width: 100%;
            font-size: 14px;
          }
        }
    }
  }
}
@media screen and (max-height : 600px) {
  .preview-ele {
    .layout-left {
        .logo {
          padding: 32px 16px 0;
          .logo-img {
            width: $logo-img-width;
            height: $logo-img-width;
            vertical-align: middle;
          }
          .name {
            padding-left: 6px;
            width: $editor-c-preview-width - $logo-img-width - 40px;
            font-size: 14px;
            vertical-align: middle;
          }
        }
    }
    .layout-right{
        .logo {
          padding: 27px 16px 0;
          text-align: center;
          .logo-img {
            width: $logo-img-width;
            height: $logo-img-width;
          }
          .name {
            width: 100%;
            font-size: 14px;
          }
        }
    }
  }
}


