@import "~layouts/CoreLayout/CoreLayout";
@import "./slider.scss";

$grid-width : 53px;
$scroll-wrap-padding : 3px;
$scroll-wrap-height : $grid-width * 6 + $scroll-wrap-padding * 2;

$preview-outer-height : $scroll-wrap-height + 135;
$header-bg-color : #3fa7ff;
$preview-header-height : 35px;
/* 滚动条展示高度计算 */

.edit-shop-container {
  position   : absolute;
  top        : 0;
  bottom     : 0;
  left       : 0;
  right      : 0;
  background : #ebeef3;
}

/**************************
 * new-mobile-editor-container
 **************************/
.editor-content {
  position   : absolute;
  z-index    : 5;
  left       : 200px;
  right      : 200px;
  top        : 50%;
  margin-top : -$preview-outer-height/2;
  padding: 10px 0;
  bottom     : 0;
  overflow   : hidden;
  .preview {
    position : relative;
    width    : 249px;
    height   : $preview-outer-height;
    margin   : 0 auto;
    .bg-phone {
      padding          : 45px 19px 0 18px;
      width            : 255px;
      height           : $preview-outer-height;
      margin           : 0 auto;
      background-image : url(http://timg.ffan.com/convert/resize/url_T1xThTB4Vv1RCvBVdK/tfs/phone.png);
      background-size  : 100%;
    }
    .scroll-wrap {
      position : relative;
      z-index  : 10;
      padding: $scroll-wrap-padding;
      height   : $scroll-wrap-height;
      overflow : hidden;
      &::-webkit-scrollbar {
        width         : 4px;
        background    : #ddd;
        border-radius : 2px;
      }
      &::-webkit-scrollbar-thumb {
        background    : #3fa7ff;
        border-radius : 2px;
      }
    }
    .slider-wrap {
      position   : absolute;
      width      : 40px;
      height     : 300px;
      right      : -80px;
      top        : 50%;
      margin-top : -150px;
      // transform: translate3d(0, -50%, 0);
    }
    .slide-text {
      position             : absolute;
      z-index              : 5;
      top                  : 50%;
      right                : -40px;
      -webkit-writing-mode : vertical-rl;
      writing-mode         : vertical-rl;
      letter-spacing       : 5px;
      color                : #555;
      text-shadow          : 1px 1px 1px #999;
      margin-top           : -150px;
      display              : inline-block;
      width                : 20px;
      text-overflow        : ellipsis;
      overflow             : hidden;
      font-size            : 12px;
    }
    .mobile-header {
      height      : $preview-header-height;
      line-height : $preview-header-height;
      text-align  : center;
      .title {
        font-size      : 12px;
        letter-spacing : 1px;
        color          : gray;
      }
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
    .shop-info {
      width  : 212px;
      min-height : 100%;
    }
  }
  .text {
    margin-top : 10px;
    font-size  : 14px;
    color      : #888;
    text-align : center;
  }
}

@media (max-width : 640px) {
  .sidebar-left {
    width : 100px;
  }
  .h5-sidebar-right {
    display : none;
  }
  .editor-content {
    right : 0;
    left  : 100px;
  }
}

.btn-block {
  position : absolute;
  bottom   : 20px;
  width    : 100%;
}

.sidebar-right {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 200px;
  background: #fff;
  z-index: 6;//todo: z-index 管理

  #detail-container {
    font-size: 14px;
    color: #666;
    .sidebar-right-title {
      padding: 15px 7px;
      font-size: 16px;
      border-bottom: 1px solid #ededed;
    }
    .icon-wechat {
      color: #05af10;
    }

  }
}



