.ele-draggable {
  padding       : 5px 10px;
  margin-bottom : 10px;
  border        : solid 1px #ddd;
  border-radius : 4px;
  user-select   : none;
  cursor: pointer;
  overflow: hidden;
  transition: background ease-in-out 0.15s,border-color ease-in-out 0.15s;
  .icon-del{
    padding-right: 10px;
    font-size: 14px;
    color: #ccc;
    &:hover{
      color: red;
    }
  }
  .icon-rangeslider{
    float: right;
    font-size: 14px;
    color: #aaa;
  }
}



