.root {
  color: black;
}
.container {
  color: #999999;
  font-size: 14px;
}

.text{
  margin: 5px;
  text-align: center;
  line-height: 34px;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
  transition: background ease-in-out .15s;
  &:hover{
    color: #fff;
    background: #108ee9;
  }
  &.selected {
    border: #0e77ca 1px solid;
    color: rgba(0, 0, 0, 0.65);
  }
}

.list {
  display: flex;
  flex-wrap: wrap;

  & > .item {
    width: 25%;
  }
}



.add-preview-btn{
  position: relative;
  z-index: 10;
  display: block;
  margin: 0 auto;
  width: 304px;
  height: 40px;
  font-size: 14px;
  color: #999;
  border: 1px solid #ccc;
  background: #fff;
  box-shadow: 0 0 24px #c7c7c7;
  transition: background ease-in-out .15s;
  outline: none;
}
.add-preview-btn:hover{
  color: #108ee9;
  border-color: #108ee9;
}
:global .add-preview-mask{
  .ant-modal-body{
    padding: 16px;
  }
  .ant-btn{
    width: 120px;
  }
}



