@mixin m-btn{
  display: inline-block;
  margin-bottom: 0;
  font-weight: 500;
  text-align: center;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  white-space: nowrap;
  line-height: 1.5;
  padding: 4px 15px;
  font-size: 12px;
  border-radius: 4px;
  user-select: none;
  transition: all .3s cubic-bezier(.645,.045,.355,1);
  position: relative;
  color: rgba(0,0,0,.65);
  background-color: #fff;
  border-color: #d9d9d9;
  border-radius: 4px;
  outline: none;
  .iconfont{
    padding-right:2px;
    vertical-align:middle;
  }
}
.m-btn{
  @include m-btn;
}

.m-btn-primary {
  color: #fff;
  background: #3fa7ff;
  border: 2px solid #3fa7ff;
}
.m-btn-default {
  color: #54a4ff;
  background: #fff;
  border: 2px solid #54a4ff;
}

.m-btn-danger{
  color: #fff;
  background: #fb4747;
  border: 2px solid #fb4747;
}
[type=button].ant-btn:focus,
[type=button].ant-btn:active {
  color: rgba(0,0,0,.65);
  background-color: #fff;
  border-color: #d9d9d9;
}


