.editor-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 50px;
  overflow-y: auto;
  & > .title {
    padding: 15px 7px;
    font-size: 16px;
    border-bottom: 1px solid #ededed;
  }
  .cm-detail {
    padding: 10px;
    
    .title {
      padding: 5px 0;
    }
    .cont {
      font-size: 12px;
      overflow: hidden;
      word-wrap: break-word;
      text-align: justify;
      word-break: break-all;
    }
    .moreBtn {
      color: #3fa7ff;
      cursor: pointer;
    }
  }
  .input-wrap {
    padding: 10px;
    overflow: hidden;
    word-wrap: break-word;
    text-align: justify;
    word-break: break-all;
    & > input {
      display: block;
      padding: 8px;
      margin: 10px 0;
      font-size: 16px;
      color: #c2c2c2;
      width: 180px;
      border: 1px solid #dedede;
      border-radius: 4px;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-break: break-all;
      overflow: hidden;
      outline: none
    }
  }
  
  .radio-wrap {
    padding: 10px;
    .item {
      display: block;
      padding: 6px 0;
      margin: 10px 0;
      text-align: center;
      cursor: pointer;
      border: 1px solid transparent;
      color: rgba(0,0,0,.65);
      background-color: #fff;
      border-color: #d9d9d9;
      border-radius: 4px;
    }
    .selected {
      background: #3fa7ff;
      color: #fff;
      border-color: transparent;
    }

  }
  .label {
    display: block;
    font-size: 16px;
    color: #000;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: break-all;
    overflow: hidden;
  }
  .desc{
    display: block;
    font-size: 14px;
    background: #fff;
  }

}
.no-editor-container {
  padding: 30px 10px;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}
:global {
  .modal.preview-modal {
    border          : rgba(255, 255, 255, .4) 10px solid;
    background      : white;
    background-clip : padding-box;
    border-radius   : 10px;
    transition      : all 0.5s ease-in-out;
  }
}


.ball-beat > div:nth-child(2n-1) {
  -webkit-animation-delay : -.35s !important;
  animation-delay         : -.35s !important;
}

.ball-beat > div {
  width             : 15px;
  height            : 15px;
  margin            : 2px;
  display           : inline-block;
  -webkit-animation : ball-beat .7s 0s infinite linear;
  animation         : ball-beat .7s 0s infinite linear;
}

.ball-beat > div,
.ball-scale-multiple > div {
  background-color : #333;
  border-radius    : 100%;
}

@-webkit-keyframes ball-beat {
  50% {
    opacity           : .2;
    -webkit-transform : scale(.75);
    transform         : scale(.75)
  }
  100% {
    opacity           : 1;
    -webkit-transform : scale(1);
    transform         : scale(1)
  }
}

@keyframes ball-beat {
  50% {
    opacity           : .2;
    -webkit-transform : scale(.75);
    transform         : scale(.75)
  }
  100% {
    opacity           : 1;
    -webkit-transform : scale(1);
    transform         : scale(1)
  }
}

.share {
  width: 130px;
  margin: 10px auto;

}
.qr {
  width: 115px;
  height: 115px;
  margin: 5px auto;
  & >  img {
    width: 100%;
    height: 100%;
    display: block;
  }
}
.qr-text {
  font-size: 13px;
  color: #c2c2c2;
  padding: 0 15px;
  text-align: center;
}
.widgets-btn-container {
  position: absolute;
  font-size: 14px;
  color: #666;
  bottom: 0;
  left: 0;
  right: 0;
  border-top: 1px solid #dedede;
  text-align: center;
  .btn {
    width: 80px;
    margin: 8px;
    font-size: 14px;
    &.active {
      color: #3fa7ff;
      border-color: #3fa7ff;
    }
  }
}

.detail{
  line-height:1.6;
  color:#555;
  font-size:14px;
  margin:10px;
}



