.editor-content {

:global {
    .rangeslider {
      position: relative;
      background: #e1e4ea;
      touch-action: none;
    }
    .rangeslider .rangeslider__fill,
    .rangeslider .rangeslider__handle {
      position: absolute
    }
    .rangeslider,
    .rangeslider .rangeslider__fill {
      display: block;
      // box-shadow: inset 0 1px 3px rgba(0, 0, 0, .3)
    }
    .rangeslider .rangeslider__handle {
      background: #fff;
      // border: 1px solid #ccc;
      cursor: pointer;
      display: inline-block;
      position: absolute;
      touch-action: none;
    }
    .rangeslider-vertical {
      height: 300px;
      max-width: 20px;
      border-radius: 10px;
      // background: #e1e4ea;
    }
    .rangeslider-vertical .rangeslider__fill {
      width: 100%;
      border-radius: 0 0 12px 12px;
      // box-shadow: none;
      bottom: 0
    }
    .rangeslider-vertical .rangeslider__handle {
      width: 20px;
      height: 80px;
      border-radius: 10px;
      // left: -2px;
    }
    .rangeslider__handle {
      &:before {
        content: " ";
        display: block;
        width: 14px;
        height: 8px;
        //background: url("src/images/icon-rangeslider.png") no-repeat;
        background: url(~images/icon-rangeslider.png) no-repeat;
        background-size: 12px 6px;
        position: absolute;
        left: 4px;
        top: 8px;
      }
      &:after {
        content: " ";
        display: block;
        width: 12px;
        height: 6px;
        background: url(~images/icon-rangeslider.png) no-repeat;
        background-size: 14px 8px;
        position: absolute;
        left: 4px;
        bottom: 8px;
      }
    }
}
}



