@mixin text-overflow{
  display: inline-block;
  word-break: break-all;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
@mixin text-overflow-all{
    display: inline-block;
    text-overflow: ellipsis;
	white-space: nowrap;
    word-break: break-all;
	display: -webkit-box;
    -webkit-line-clamp: 1;
}
@mixin word-break-all{
  word-break: break-all;
}


