.list-control {
    float: right;
    padding-top: 10px;
    color: #2e93fb;
    .showAll,.cancelAll{
      padding: 0 8px;
      height: 21px;
      line-height: 20px;
      border: 1px solid #2e93fb;
      border-radius: 4px;
      cursor: pointer;
      transition: background ease-in-out .15s;
      &:hover{
        color: #fff;
        background: #2e93fb;
      }
    }
  }
.filter-container{
  position: absolute;
  top: 40px;
  bottom: 38px;
  left: 0;
  right: 0;
  margin: 0 5px;
  padding-top: 10px;
  overflow-x: hidden;
  &::-webkit-scrollbar{
    width: 8px;
  }
  &::-webkit-scrollbar-track{
    background: #00162b;
  }
  &::-webkit-scrollbar-thumb{
    background: #334555;
    border-radius: 6px;
  }
  .h5-fs-product {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    .fs-product {
      padding: 0;
      width: 50%;
    }
  }
  .product-image {
    position: relative;
    padding: 0 5px;
    .preview-image{
      width: 100%;
      // modify by leiming 限高
      max-height: 173.47px;
    }
  }
  .product-title {
    text-align: center;
    color: white;
    position: absolute;
    right: -2px;
    bottom: -10px;
    cursor: pointer;
    display: none;
    input {
        display: none;
    }
    .selectedImg {
        width: 30px;
        height: 30px;
    }
  }
  .selected {
    .product-image {
      .product-title {
        display: block;
      }
      .product-shadow {
        display: none;
      }
      .preview-image {
        outline: 3px solid #ffa800;
      }
    }
  }
  .product-shadow {
    width: 100%;
    height: 100%;
    background: #262c31;
    opacity: 0.8;
    display: block;
    position: absolute;
    cursor: pointer;
    left: 0;
    top: 0;
    z-index: 1;
  }
}
.h5-fs-product{
  .fs-product{
    padding: 30px;
    text-align: center;
  }
  .product-image{
    &>img{
      width: 100%;
    }
  }
  .product-name {
    display: block;
    margin: 8px 0 12px;
    font-size: 14px;
    color: #fff;
  }
  .fs-product:hover:not(.selected) {
    .product-image {
      .preview-image {
        box-shadow: 0 0 4px 4px #ffa800;
      }
    }
  }
}



