.h5-bg-qr {
    text-align: center;
    .title {
        font-size: 16px;
        padding-bottom: 20px;
    }
    .desc {
        color: #999;
    }
}
.ant-confirm-body .ant-confirm-content {
  margin-left : 0;
}
.footer {
    position: fixed;
    bottom: 0;
    padding-top: 2px;
    background: rgba(255,255,255,0.8);
    text-align: center;
    .ant-btn {
        width: 100px;
        padding: 4px 10px;
        margin: 0 4px 4px 0;
        transition: background ease-in-out 0.15s,border-color ease-in-out 0.15s;
        .iconfont {
            margin-right: 4px;
            color: #999;
        }
        &:hover {
            background: #2692fb;
            border-color: #2692fb;
            color: #fff;
            .iconfont {
                color: #fff;
            }
        }
    }
}


