@media screen and (min-height: 601px){
  .edit-shop-warp{
    .header{
      display: block;
    }
    .sidebar-left,.h5-sidebar-right{
      top: 42px;
    }
  }
}
@media screen and (max-height: 600px){
  .edit-shop-container{
    top: 0;
  }
  .edit-shop-warp{
    .header{
      display: none;
    }
    .sidebar-left,.h5-sidebar-right{
      top: 0;
    }
  }
}


