.root {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  width: 200px;
  background: #262c31;
  .list {
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 40px;
    .item {
      .content {
        position: absolute;
        left: 0;
        right: 0;
        top: 42px;
        bottom: 0;
      }
    }
  }
  .more-btn {
    display: block;
    width: 100%;
    background: #2e363d;
    border-bottom: 1px solid #1a1e2a;
    overflow: hidden;
    height: 40px;
    line-height: 40px;
    font-size: 12px;
    cursor: pointer;
    color: #748094;
    background: #2e363d;
    position: absolute;
    left: 0;
    bottom: 0;
    i {
      margin: 0 5px;
      &.right {
        float: right;
      }
    }
  }
}



