.img-container {
  padding: 10px 0;
}

.ant-upload-select{
  display: block !important;
}
.avatar-uploader-w{
  max-height: 100px;
  overflow: hidden;
  img{
    width: 100%;
    // max-height: 100px;
  }
}
.avatar-uploader-h{
  height: 100px;
  overflow: hidden;
  img{
    width: auto;
    height: 100px;
  }
}

.avatar-uploader {
  text-align    : center;
  display       : block;
  border        : 1px dashed #d9d9d9;
  border-radius : 6px;
  cursor        : pointer;
  color          : #999;
}

.avatar-uploader-trigger {
