html {font-size: 100px;}
@media (min-width: 480px) {
  html {font-size: 110px !important;}}
@media (min-width: 640px) {
  html {font-size: 120px !important;}}
@media (min-width: 720px) {
  html {font-size: 130px !important;}}

.order-tabs-nav-wrap{
  .ant-tabs-bar{
    background-color: #f3f4f6;
    border-bottom: 0;
    margin-bottom: 0;
  }
  .ant-tabs-nav{
    width: 100%;
  }
  .ant-tabs-nav-scroll{
    height: .44rem;
    font-size: .16rem;
    .ant-tabs-ink-bar{
      display: none!important;
    }
    .ant-tabs-tab{
      margin-right: 0;
      width: 50%;
      text-align: center;
      color: #000;
    }
    .ant-tabs-tab-active{
      color: #2692fb;
    }
  }
}
.order-tabs-tabpane{
  @mixin flex{
    display: flex;
    justify-content: space-between;
  }
  li{
    padding-bottom: .06rem;
    background-color: #f3f4f6;
  }
  .store-status-info{
    @include flex;
    padding: 0 .1rem;
    height: .35rem;
    line-height: .35rem;
    border-top: 1px solid #ebebeb;
    border-bottom: 1px solid #ebebeb;
    background-color: #fff;
    .time{
      display: block;
      font-size: .14rem;
      color: #000;
    }
    .status{
      display: block;
      font-size: .16rem;
      color: #2692fb;
    }
  }
  .store-details-info{
    @include flex;
    padding: .09rem .1rem .1rem;
    border-bottom: 1px solid #ebebeb;
    background-color: #fff;
    .img{
      width: .75rem;
      height: .75rem;
    }
    
  }
  .store-price{
    @include flex;
    flex-direction: column;
    align-items: flex-end;
    .price{
      font-size: .18rem;
      color: #000;
      span{
        font-size: .12rem;
      }
    }
    .details{
      padding: 0 .05rem;
      height: .2rem;
      line-height: .2rem;
      font-size: .13rem;
      color: #fff;
      background-color: #33a1ff;
      border-radius: 2px;
      .iconfont{
        font-size: .12rem;
      }
    }
  }
}


