@import "../../Elements/base.scss";
.ele {
    overflow: hidden;
    .detail-title {
        @include detailTitle;
    }
    .item {
        display: inline-block;
        .item-img {
            width: 83px;
            height: 42px;
            cursor: pointer;
        }
        .row-radio {
            position: relative;
            margin: 6px 0;
            cursor: pointer;
            font-size: 12px;
            color: $radio-color;
            .radio-btn {
                margin-right: 2px;
                vertical-align: -3px;
            }
        }
        .radio-input {
            position: absolute;
            top: 2px;
            left: -3px;
            width: 16px;
            height: 16px;
            opacity: 0;
            cursor: pointer;
            z-index: 1;
        }
        @include rowRadio($radio-color);
    }
    .item:nth-child(odd) {
        margin-right: 14px;
    }
}



