.select-modal {
  .ant-modal-body {
    padding: 0;
  }
  .tools-modal-content {
    padding: 16px;
  }
}
.modal-header {
  display: flex;
  padding: 10px;
  align-items: center;
  background: #108ee9;
  color: #fff;
  & > h3 {
    flex: 1;
    text-align: center;
  }
  .close-btn {
    width: 20px;
    height: 20px;
    font-style: normal;
    font-size: 16px;
    line-height: 1;
    cursor: pointer;
  }
  .save-btn {
    display: inline-block;
    cursor: pointer;
  }
}
.modal-footer {
  text-align: center;
  padding-bottom: 16px;
  .ant-btn:last-child {
    margin-left: 20px;
  }
}

.tools-modal-content {
  .tool {
    display: inline-block;
    vertical-align: top;
    overflow: hidden;
    width: 33%;
    margin: 10px 0;
  }
  .preview-wrap {
    width: 64px;
    height: 64px;
    border-radius: 10px;
    overflow: hidden;
    float: left;
    text-align: center;
    background: #dedede;
  }
  .preview {
    display: block;
    width: 100%;
    height: 100%;
  }
  .info {
    overflow: hidden;
    padding-left: 10px;
  }

  .name {
    margin-bottom: 10px;
  }

  .btn {
    display: inline-block;
    width: 60px;
    text-align: center;
    padding: 3px 0;
    border-radius: 2px;
    font-size: 14px;
    line-height: 100%;
    border: 1px solid #d9d9d9;
    cursor: pointer;
    // &:active {
    //   color: #fff;
    //   background-color: #108ee9;
    //   border-color: #108ee9;
    // }
  }
  
  .btn.active {
    color: #fff;
    background-color: #108ee9;
    border-color: #108ee9;
  }
}


