@import "../../../../styles/base.scss";

html {font-size: 100px;}
@media (min-width: 480px) {
    html {font-size: 110px !important;}}
@media (min-width: 640px) {
    html {font-size: 120px !important;}}
@media (min-width: 720px) {
    html {font-size: 130px !important;}}
.memberCard-block {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #f3f4f6;
}
.memberCard-box{
  padding: .2rem .25rem .04rem;
  background-color: #f3f4f6;
  width: 100%;
  border-bottom: 1px solid #e7e7e7;
  .memberCard-container{
    position: relative;
    margin-bottom: .04rem;
    .memberCard-info{
      position: absolute;
      top: 50%;
      left: 0;
      padding: 0 .4rem;
      transform: translateY(-50%);
      overflow: hidden;
    }
    .storeLogo-img{
      float: left;
      & > img{
        width: .54rem;
        border-radius: 50%;
      }
    }
    .store-name,.store-address{
      padding-left: .1rem;
      width: 75%;
      @include text-overflow;
    }
    .store-name{
      font-size: .2rem;
      color: #fff;
    }
    .store-address{
      font-size: .12rem;
      color: rgba(255,255,255,0.5);
    }

    .memberCard-tel{
      position: absolute;
      bottom: .3rem;
      left: 0;
      right: 0;
      padding: 0 .4rem;
      font-size: .2rem;
      color: rgba(255,255,255,0.5);
      letter-spacing: .06rem;
      
      display: flex;
      justify-content: space-between;
      & > span{
        display: inline-block;
      }
      // & > span{
      //   padding-right: .3rem;
      // }
      // & > span:last-child{
      //   padding-right: 0;
      // }
    }
    .memberCard-bg{
      display: block;
      width: 100%;
      border-radius: 4px;
    }
  }
  .memberCard-shadow{
    border-radius: 4px 4px 0 0;
    height: .2rem;
    background: linear-gradient(rgba(0,0,0,.15),transparent);
  }
}
.memberCard-cardnum{
  color: #000;
  text-align: center;
  background: #fff;
  margin-top: .2rem;
  padding: .2rem 0;
  .title{
    font-size: .15rem;
    color: #666;
    line-height: 0.4rem;
  }
  .card-num{
    font-size: .14rem;
    span {
      margin: 0 .05rem;
    }
  }
}
.get-card{
  padding: .2rem .25rem .15rem;
}
.get-memberCard-btn{
  display: block;
  margin: .1rem auto 0;
  width: .88rem;
  height: .25rem;
  line-height: .25rem;
  border: 1px solid #33a1ff;
  border-radius: 4px;
  font-size: .14rem;
  font-weight: 600;
  color: #33a1ff;
  text-align: center;
}


