$font-gray : #999999;
$font-blue : #2692fb;
$col-background-dark : #fafafa;
$col-border-color : #e7e7e7;
$font-size-big : 16px;
$font-size-usual : 14px;
$font-size-small : 12px;
$activity-width : 380px;
$activity-title-margin-right : 10px;
.activity-component {
  // 父容器
}

.input-container,
.text-container,
.text-area-container {
  width : $activity-width;
  //margin-bottom: 5px;
}

.input-container {
  margin-bottom : 8px;
}

.text-container {
  .title {
    margin-right : $activity-title-margin-right;
    font-size    : $font-size-small;
  }
  .desc {
    padding   : 2px 0;
    font-size : $font-size-small;
    color     : $font-gray;
  }
}

.upload-img-container {
  position : absolute;
  right    : 10px;
  top      : 10%;
}

.upload-img-container:before {
  position    : absolute;
  color       : red;
  margin-left : -20px;
  top         : 40%;
  content     : "*";
}

.upload-img-container.color-gray:before {
  color: rgba(0, 0, 0, 0.247059);
}

.activity-container {
  font-size  : $font-size-small;
  margin-top : 8px;

  .title {
    margin-right : $activity-title-margin-right;
  }

}

.text-area-container {
  align-content : center;
  display       : flex;

  .title {
    font-size    : $font-size-small;
    align-self   : center;
    flex         : 0 1 auto;
    margin-right : $activity-title-margin-right;
  }
  .content {
    flex : 1 1 auto;
  }
}

.input-container {
  align-content : center;
  display       : flex;

  .title {
    font-size    : $font-size-small;
    align-self   : center;
    flex         : 0 1 auto;
    margin-right : $activity-title-margin-right;
  }
  .content {
    flex : 1 1 auto;
  }
}



