$font-gray : #999999;
$font-blue : #2692fb;
$col-background-dark : #fafafa;
$col-border-color : #e7e7e7;
$font-size-big : 16px;
$font-size-usual : 14px;
$font-size-small : 12px;

#publish-container {

  .color-red {
    line-height: 20px;
    font-size: 12px;
    display: inline-block;
    width: 10px;
    color: red;
  }

  .color-red.color-gray {
    color: rgba(0, 0, 0, 0.247059);
  }

  font-size : $font-size-usual;

  .header {
    padding     : 20px;
    line-height : 24px;

    .title {
      color : $font-gray;
    }

    .title-content {
      color     : $font-blue;
      font-size : $font-size-big;
    }
  }

  .ant-row-flex {
    padding    : 10px;
    border-top : $col-border-color 1px solid;
  }

  .ant-row-flex:nth-child(odd) {
    background : $col-background-dark;
  }

  .ant-input-wrapper {
    margin-bottom : 5px;
  }

  .ant-row {
    margin-bottom  : 50px;
    vertical-align : middle;
  }

  .ant-col-4 {
    height         : 100px;
    line-height    : 100px;
    vertical-align : middle;
  }

  .summary {
    text-align   : right;
    background   : $col-background-dark;
    border-top   : $col-border-color 1px solid;
    padding      : 5px 10px 5px 0;
    margin-left  : -8px;
    margin-right : -8px;

    .num {
      color         : red;
      padding-left  : 7px;
      padding-right : 7px;
      font-size     : 24px;
    }
  }

  .footer-container {
    text-align : center;
    padding    : 10px 0;

    .btn {
      margin        : 0 10px;
      padding       : 5px 40px;
      border-radius : inherit;
    }

    .btn-blue {
      color        : $font-blue;
      border-color : $font-blue;
    }
  }

}



