body {
  background: #fff;
}

#createActivity {
  max-width: 960px;
  min-width: 680px;
  padding-top: 30px;
  background: #fff;
  margin: 0 auto;
  .steps-container {
    width: 75%;
    margin: 20px auto;
  }
  .ant-steps .ant-steps-head-inner {
    line-height: 24px;
  }

}


.create-activity-container {

  .form-content {
    margin-top: 20px;
    padding-top: 10px;
  }
  .spec {
    color: #a9a9a9;
    margin-bottom: 0;
    label {
      color: #a9a9a9;
    }
  }
  .act-name {
    border-color: transparent;
    border-bottom-color: #d9d9d9;
    border-radius: 0;
    &:focus,
    &:hover {
      box-shadow: 0 0 0 2px rgba(16, 142, 233, .2);
      box-shadow: none;
      border-color: transparent;
      border-bottom-color: #49a9ee;
    }
  }
  .act-tools-item {
    position: relative;
    display: inline-block;
    padding: 4px 15px;
    // width: 120px;
    line-height: 1.5;
    border: 1px solid #49a9ee;
    color: #49a9ee;
    margin-right: 20px;
    .close-btn {
      position: absolute;
      right: -8px;
      top: -8px;
      width: 16px;
      height: 16px;
      display: flex;
      font-size: 14px;
      line-height: 100%;
      align-items: center;
      justify-content: center;
      font-style: normal;
      border-radius: 50%;
      color: #fff;
      background: #d9d9d9;
      cursor: pointer;
    }
  }
  .add-tool-btn {
    width: 100px;
  }
  .form-actions {
    text-align: center;
    .ant-btn {
      width: 150px;
      border-radius: 0;
    }
    .ant-btn:last-child {
      margin-left: 30px;
      color: #49a9ee;
      border-color: #49a9ee;
    }
  }

}

.act-config-container {
  .header {
    padding-left: 20px;
    .act-name {
      margin-left: 5px;
      font-size: 14px;
      color: #49a9ee;
    }
  }
  .act-tools-list {
    width: 100%;
    margin: 10px 0 20px;
    .item {
      display: flex;
      border-top: 1px solid #eee;
      padding: 20px;
      &:last-child {
        border-bottom: 1px solid #eee;
      }
    }
    .preview-wrap {
      width: 100px;
      height: 100px;
      border-radius: 16px;
      margin-right: 20px;
      overflow: hidden;
      background: #dedede;
    }
    .preview {
      display: block;
      width: 100%;
      height: 100%;
    }
    .tool_info {
      flex: 1;
      margin-right: 30px;
      .title {
        font-size: 16px;
      }
      .desc {
        min-height: 30px;
        font-size: 14px;
        word-wrap: break-word;
        word-break: break-all;
        overflow: hidden;
        text-overflow: ellipsis;
        line-height: 1;
        margin: 5px 0;
      }

    }

  }
  .step-actions {
    display: flex;
    align-items: center;
    justify-content: center;

    .ant-btn {
      width: 150px;
      border-radius: 0;
    }
    .ant-btn:last-child {
      margin-left: 30px;
      color: #49a9ee;
      border-color: #49a9ee;
    }
  }
}
.ant-popover-inner {
  .ant-popover-inner-content {
    padding: 8px;
    .qrcode-img {
      display: block;
      width: 128px;
      height: 128px;
    }
  }
}



