.react-grid-layout {
  // padding: 1px;
  position: relative;
  transition: height 200ms ease;
  width: 100%;
}

.react-grid-item {
  transition: all 200ms ease;
  background: #cccccc;
  border: solid 2px white;
  transition-property: left, top;
  overflow: hidden;
}

.react-grid-item.cssTransforms {
  transition-property: transform;
}

.react-grid-item.resizing {
  z-index: 1;
}

.react-grid-item.react-draggable-dragging {
  transition: none;
  z-index: 3;
}

.react-grid-item.react-grid-placeholder {
  background: red;
  opacity: 0.2;
  transition-duration: 100ms;
  z-index: 2;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.react-grid-item>.react-resizable-handle {
  position: absolute;
  width: 12px;
  height: 12px;
  bottom: 0;
  right: 0;
  background: url('data:image/svg+xml;base64,');
  background-position: bottom right;
  background-size: 100%;
  padding: 0 3px 3px 0;
  background-repeat: no-repeat;
  background-origin: content-box;
  box-sizing: border-box;
  cursor: se-resize;
}

.react-resizable {
  position: relative;
}

.shop-info {
  width: 212px;
  min-height: 100%;

  .selected {
    z-index: 10;
    box-shadow: 0 0 2px 2px dodgerblue;
    // outline: 5px solid darkcyan;
    border: solid 2px darkturquoise;
  }
  .delete-btn {
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    color: #f7f7f7;
    font-size: 16px;
    width: 20px;
    height: 20px;
    background: #fff;
    text-align: center;
    line-height: 1.0;
    background: rgba(5, 9, 13, 0.5);
  }

  .delete-btn.show {
    display: block;
  }
}



