$logo-name-color:#fff;
$address-color:#fff;
$radio-color:#3fa7ff;

/*editor-content-preview盒子模型*/
$editor-content-padding : 20px;
$editor-c-preview-width : 304px;
$editor-c-preview-height : 510px;
$editor-c-preview-margin : $editor-c-preview-height / 2;

$editor-preview-base-height : 74px;
$editor-preview-base-small-height : $editor-preview-base-height;


@mixin detailTitle{
    margin    : 15px 0 7px;
    font-size : 12px;
    color     : #666666;
}
@mixin input{
    outline            : none;
    padding            : 4px 6px;
    border             : 1px solid #ccc;
    -webkit-box-shadow : inset 0 1px 1px rgba(0, 0, 0, .075);
    box-shadow         : inset 0 1px 1px rgba(0, 0, 0, .075);
    -webkit-transition : border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    -o-transition      : border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    transition         : border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    &:focus {
      border-color       : #66afe9;
      outline            : 0;
      -webkit-box-shadow : inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(102, 175, 233, .6);
      box-shadow         : inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(102, 175, 233, .6);
    }
}
@mixin text-overflow{
  display: inline-block;
  word-break: break-all;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
@mixin text-overflow-all{
    display: inline-block;
    text-overflow: ellipsis;
	white-space: nowrap;
    word-break: break-all;
	display: -webkit-box;
    -webkit-line-clamp: 1;
}
@mixin word-break-all{
  word-break: break-all;
}
@mixin flexRowWrap{
    display         : flex;
    flex-direction  : row;
    flex-wrap       : wrap;
}
@mixin flexCenter{
    display: flex;
    align-items: center;
}
@mixin flexCenterBetween{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
@mixin displayMiddle{
    display: inline-block;
    vertical-align: middle;
}
@mixin rowRadio($color){
    i.icon-radio2 {
        display: none;
    }
    i.icon-radio1 {
        display: inline-block;
    }
    .radio-input:checked + .radio-btn i.icon-radio2 {
        display: inline-block;
        color: $color;
    }
    .radio-input:checked + .radio-btn i.icon-radio1 {
        display: none;
    }
}
@mixin colPreviewTitle{
    display: inline-block;
    @include word-break-all;
    overflow: hidden;
}


