@import "../../../../../../../../styles/base.scss";

html {font-size: 100px;}
@media (min-width: 480px) {
  html {font-size: 110px !important;}}
@media (min-width: 640px) {
  html {font-size: 120px !important;}}
@media (min-width: 720px) {
  html {font-size: 130px !important;}}

.commodity-list{
  margin-bottom: .1rem;
  // padding-bottom: .07rem;
  // border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
  background-color: #fff;
  .item{
    // display: flex;
    // justify-content: space-between;
    padding: .07rem .1rem 0;
    overflow: hidden;
  }
  .commodity-pic{
    position: absolute;
    display: block;
    width: .75rem;
    height: .75rem;
  }
  .commodity-store-info{
    margin-left: .75rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1;
    padding: 0 0 .06rem .14rem;
    height: .84rem;
    border-bottom: 1px solid #ebebeb;
  }
  .commodity-name{
    font-size: .15rem;
    color: #000;
    white-space: nowrap;
    // width: 2.8rem;
    // word-break: break-all;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .commodity-buy-box{
    display: flex;
    justify-content: space-between;
  }
  .commodity-price{
    display: block;
    font-size: .18rem;
    color: #2692fb;
    span{
      font-size: .1rem;
    }
  }
  .commodity-buy{
    display: block;
    padding-right: .04rem;
    width: .5rem;
    height: .2rem;
    font-size: .13rem;
    color: #fff;
    background-color: #33a1ff;
    border-radius: .02rem;
    border: none;
    background-image: url(http://timg.ffan.com/convert/resize/url_T1k3ETBKAT1RCvBVdK/tfs/1.png);
    background-size: .05rem auto;
    background-position: .4rem center;
    background-repeat: no-repeat;
  }
}



