@import "../../Elements/base.scss";
$btn-hover-background:#0e77ca;


.ele-container {
  padding: 0 6px;
  background: #fff;
  @include flexRowWrap;
}

.preference-container {
    padding  : 0 10px;
    .detail-title {
        @include detailTitle;
    }
    .grid-item {
        background: #eee;
        margin-bottom: 20px;
        padding: 10px 5px 0;
        border: 1px solid #ddd;
        position: relative;
        .del {
            position: absolute;
            right: -12px;
            top: -12px;
            display: block;
            width: 24px;
            height: 24px;
            border-radius: 50%;
            background: #fff url(../../../../../../images/icon-closebtn.png);
            background-size: 100% 100%;
            cursor: pointer;
            &::before{
                margin-bottom: 10px;
            }
        }
    }
    .item-action {
        margin-bottom: 20px;
        .add {
            width: 100%;
            height: 30px;
            border-radius: 4px;
            background: #2692fb;
            color: #fff;
            border: 1px solid #2692fb;
            cursor: pointer;
            &:hover {
                background: $btn-hover-background;
                border-color: $btn-hover-background;
            }
            i {
                font-size: 14px;
                vertical-align: -1px;
                margin-right: 4px;
            }
        }
    }
}
.blank-template{
  padding: 10px;
  font-size: 14px;
  color: #888;
  text-align: center;
  .blank-template-img{
    display: inline-block;
    width: 76px;
  }
  .blank-template-info{
    
  }
}




