@import '../../routes/H5Editor/Elements/base.scss';

html {font-size: 100px;}
@media (min-width: 480px) {
  html {font-size: 110px !important;}}
@media (min-width: 640px) {
  html {font-size: 120px !important;}}
@media (min-width: 720px) {
  html {font-size: 130px !important;}}

.home-ele{
  img{
    width: 100%;
    display: block;
  }
  .basic-info{
    position: relative;
    font-size: .07rem;
    color: #fff;
    .logo-img{
      width: .5rem;
      height: .5rem;
    }
    .name{
      font-size: .14rem;
      font-weight: 700;
      color: #fff;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .contact{
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 0 10px;
      height: 34px;
      background: hsla(0,0%,100%,.3);
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
      align-items: center;
      -ms-flex-pack: justify;
      justify-content: space-between;
      .address{
        padding-right: 5px;
        width: 90%;
        color: #fff;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
        align-items: center;
        .site{
          display: inline-block;
          margin-right: 5px;
          width: .1rem;
          height: .14rem;
        }
        .site-text{
          font-size: .14rem;
          display: inline-block;
          word-break: break-all;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          text-shadow: 1px 1px 1px rgba(0,0,0,.6);
        }
      }
      .contact-phone{
        padding: 6px 0 6px 10px;
        border-left: 1px solid hsla(0,0%,100%,.3);
        .contact-tel{
          width: 16px;
          height: 16px;
        }
      }
    }
  }
  .basic-info.layout-left{
    .logo{
      padding: 0 .2rem;
      position: absolute;
      top: 30%;
      left: 0;
      width: 100%;
      height: .5rem;
    }
    .logo-img{
      position: absolute;
    }
    .name{
      padding: 0 .1rem 0 .6rem;
      line-height: .5rem;
    }
  }
  .basic-info.layout-right{
    .logo{
      width: 100%;
      padding: 0 .2rem;
      position: absolute;
      top: 30%;
      left: 0;
      text-align: center;
    }
    .logo-img{
      display: inline-block;
      margin-bottom: .04rem;
    }
    .name{
      padding: 0 .1rem;
    }
  }

  .basic-info-s2{
    // height: $editor-preview-base-height * 2.9;
    .logo-img-s{
      width: .35rem;
      height: .35rem;
    }
    .name-s{
      display: block;
      font-size: 16px;
      color: #000;
      @include text-overflow;
    }
    .contact-s{
      padding: 4px 10px;
      font-size: 12px;
      color: #666;
      .address, .contact-phone{
        position: relative;
      }
      .site,.contact-tel{
        position: absolute;
        top: 2px;
        display: inline-block;
        width: 9px;
        vertical-align: middle;
      }
      .site-text{
        padding-left: .12rem;
        width: 100%;
        @include text-overflow;
        vertical-align: middle;
      }
      .name{
        padding-left: 86px;
        width: 288px;
        font-size: 16px;
        color: #000;
        @include text-overflow;
      }
    }
  }
  .basic-info-s2.layout-left{
    .title{
      position: relative;
    }
    .logo-img-s{
      position: absolute;
      bottom: 6px;
      left: 10px;
      width: 70px;
      height: 70px;
    }
    .name-s{
      padding-left: 86px;
      width: 100%;
      font-size: 16px;
      color: #000;
      @include text-overflow;
    }
  }
  .basic-info-s2.layout-right{
    position: relative;
    .title{
      position: absolute;
      bottom: .45rem;
      left: 50%;
      transform: translateX(-50%);
    }
    .logo-img-s{
      margin: 0 auto;
      display: block;
      width: 70px;
      height: 70px;
    }
    .name-s{
      margin: .06rem auto 0;
      width: 276px;
      text-align: center;
    }
    .contact-s{
      margin-top: .4rem;
    }
  }

  .card-s2{
    position: relative;
    margin-top: .1rem;
    padding: 0 10px;
    .title{
    padding-bottom: 6px;
    font-size: 14px;
    color: #333;
    }
    .card-img{
      display: block;
      border-radius: 6px;
    }
    .memberCard-info{
      position: absolute;
      top: 42%;
      left: 0;
      padding: 0 .5rem;
      transform: translateY(-50%);
      overflow: hidden;
    }
    .storeLogo-img{
      float: left;
      & > img{
        width: .54rem;
        border-radius: 50%;
      }
    }
    .store-name,.store-address{
      padding-left: .1rem;
      width: 68%;
      @include text-overflow;
    }
    .store-name{
      font-size: .2rem;
      color: #fff;
    }
    .store-address{
      font-size: .12rem;
      color: rgba(255,255,255,0.5);
    }
    .img-shadow{
      margin-top: .06rem;
      height: .22rem;
      background: linear-gradient(rgba(0,0,0,.15),transparent);
      border-radius: 6px 6px 0 0;
    }
    .get-memberCard-btn{
      display: block;
      margin: .1rem auto 0;
      width: 1.2rem;
      height: .25rem;
      line-height: .25rem;
      border: 1px solid #33a1ff;
      border-radius: 4px;
      font-size: .14rem;
      font-weight: 600;
      color: #33a1ff;
      text-align: center;
    }
  }
}
.home-ele{
  .ele-container-app {
    padding: .04rem .1rem 0;
    @include flexRowWrap;
  }
  .ele-col-1-app {
    width: 100%;
    margin-bottom: .04rem;
    .item {
      height: .9rem;
      background-size: cover!important;
      background-position: 50%!important;
    }
  }
  .ele-col-4-app {
    width: 22.7%;
    margin-right: 3%;
    margin-top: -.04rem;
    &:nth-child(4n) {
      margin-right: 0;
    }
    .item {
      height: 0.7rem;
      background-size: cover!important;
      background-position: 50%!important;
    }
  }
  .ele-col-4-img-r-app {
    width: 25%;
    // margin-bottom: 0.1rem;
    .item {
      height: 0.66rem;
      background-size: 0.5rem 0.5rem!important;
      text-align: center;
      .title {
        width: 0.5rem;
        height: 0.18rem;
        margin-top: 0.5rem;
        @include colPreviewTitle;
      }
    }
  }
  .ele-col-2-img-r-app {
    width: 48.5%;
    margin-bottom: .08rem;
    margin-right: 3%;
    &:nth-child(2n) {
      margin-right: 0;
    }
    .item {
      background-size: auto 90%!important;
      height: .55rem;
      line-height: .55rem;
      padding-left: .1rem;
      .title {
        height: .55rem;
        @include colPreviewTitle;
      }
    }
  }
}



