@import "../../../H5Editor/Elements/base";

.preview {
  position : relative;
  margin:  0 auto;
  padding: 4px;
  width: $editor-c-preview-width;
  min-height: $editor-c-preview-height - 80px;
  box-shadow: 0 0 24px #adaeaf;
  background: #fff;
  // overflow-x: hidden;
  .blank-bg {
    width:100%;
  }
  &::-webkit-scrollbar {
    display: none;
  }
  .selected-ele{
    position: relative;
    z-index: 2;
    box-shadow: 0 0 4px 4px #ffa800;
  }
  .fast-menu {
    position: absolute;
    top : 0;
    right: -30px;
    .btn{
      display: block;
      margin-bottom: 4px;
      width: 24px;
      height: 22px;
      border: 1px solid #bbb;
      border-radius: 2px;
      background-color: #fff;
      box-shadow: 0 0 4px rgba(0,0,0,0.2);
      .iconfont{
        font-size: 14px;
        color: #aaa;
      }
      &:hover{
        border: 2px solid #ffa800;
        cursor: pointer;
        .iconfont{
          color: #ffa800;
        }
      }
    }
  }
}



