@import '../../../Mobile/routes/H5Editor/Elements/base.scss';

.t-container {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-x: hidden;

  height     : 100%;
  background : rgba(50,50,50,.1);
  .row-radio { position : relative; line-height: 32px;}
  i.icon-radio { display : none; margin-right : 4px; font-size : 16px; color : #2692fb; }
  i.icon-radio1 { display : inline-block; margin-right : 4px; font-size : 16px; color : #ccc; }
  input[type="radio"] { position : absolute; top : 2px; left : 0; width : 16px; height : 18px; opacity : 0; cursor : pointer; z-index : 1; }
  input[type="radio"]:checked + span i.icon-radio { display : inline-block; }
  input[type="radio"]:checked + span i.icon-radio1 { display : none; }
}

.phone-box {
  $phone-box-width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  padding-top: 40px;
  padding-bottom: 40px;
  width: 1100px;
  // padding : 0 30px;
  li {
    float: left;
    // margin: 20px 12px 0;
    margin: 43px 30px 0;
    //width: $phone-box-width / 6 - 2%;
    // width: 172px;
    width: 160px;
    position: relative;
    //overflow: hidden;
  }
  .control-list {
    position: absolute;
    top: 0;
    right: -24px;
    a {
      display: block;
      width: 25px;
      height: 25px;
      border: 1px solid #ccc;
      text-align: center;
      .iconfont {
        vertical-align: middle;
        display: inline-block;
      }
    }
  }
  .preview-defaultPage{
    height: 305px;
    font-size: 14px;
    text-align: center;
    border: 1px solid #aaa;
    background: #fff;
    cursor: pointer;
    .icon-add{
      display: block;
      margin-top: 100px;
      font-size: 44px;
    }
  }
  .preview-img{
    position: relative;
    height: 305px;
    border: 1px solid #aaa;
    background: #fff;
    overflow: hidden;
    img{
      display: block;
      width: 100%;
    }
    .preview-des {
      width: 100%;
      position: absolute;
      top: 180px;
      text-align: center;
      color: #fff;
      font-size: 17px;
    }
    .active{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,0.6);

      animation: myPreviewImgHover 0.5s 1;
      -moz-animation: myPreviewImgHover 0.5s 1;
      -webkit-animation: myPreviewImgHover 0.5s 1;
      img{
        position: absolute;
        top: 40px;
        left: 50%;
        transform: translateX(-50%);
        width: 120px;
        height: 120px;
      }
    }
    .normal{
      position: absolute;
      bottom: -160px;
      left: 0;
      width: 100%;
      height: 160px;
      background: rgba(0,0,0,0.6);

      animation: myPreviewImgLeave 0.4s 1;
      -moz-animation: myPreviewImgLeave 0.4s 1;
      -webkit-animation: myPreviewImgLeave 0.4s 1;
      img{
        position: absolute;
        bottom: 20px;
        left: 50%;
        transform: translateX(-50%);
        width: 120px;
        height: 120px;
      }
    }
  }
  .preview-text{
    display: inline-block;
    width: 116px;
    vertical-align: top;
    @include text-overflow;
  }
}
@keyframes myPreviewImgHover{
  from
   {bottom:-100%;}
  to
   {bottom:0;}
}

@keyframes myPreviewImgLeave{
  from
  {bottom: 0;}
  to
  {bottom: -100%;}
}

.phone {
  position        : relative;
  width           : 172px;
  height          : 327px;
  background      : url(~images/bg_iphone6.png) no-repeat;
  background-size : 100% 100%;
}

.module-one, .module-two, .module-three {
  position    : absolute;
  left        : 50%;
  top         : 30px;
  margin-left : -74px;
  width       : 149px;
  height      : 265px;
  border      : 1px solid #e5e5e5;
}

.module-one {
  background      : url(~images/phone-module1.jpg) no-repeat;
  background-size : 100%;
}

.module-two {
  background      : url(~images/phone-module2.jpg) no-repeat;
  background-size : 100%;
}

.module-three {
  background      : url(~images/phone-module3.jpg) no-repeat;
  background-size : 100%;
}

.radio-active {
  color : #2692fb;
}

.btn-box {
  margin-top : 70px;
  text-align : right;
}

.button {
  width         : 130px;
  height        : 40px;
  color         : #fff;
  font-size     : 16px;
  text-align    : center;
  line-height   : 40px;
  background    : #08d9c2;
  border-radius : 16px;
  border        : none;
}




