.root {
  display: flex;
  width: 100%;
  background: #2e363d;
  border-bottom: 1px solid #1a1e2a;
  overflow: hidden;
  height: 40px;
  line-height: 40px;
  font-size: 12px;
  color: white;
  &.bottom {
    position: absolute;
    left: 0;
    bottom: 0;
  }
  i {
    margin: 0 5px;
    &.right {
      float: right;
    }
  }
  .label {
    flex: 1;
  }
  .managerBtn {
    margin-right: 10px;
    cursor: pointer;
  }
}



