@import "../../../../styles/base.scss";

html {font-size: 100px;
  @media (min-width: 480px) {
    html {font-size: 110px !important;}}
  @media (min-width: 640px) {
    html {font-size: 120px !important;}}
  @media (min-width: 720px) {
    html {font-size: 130px !important;}}
}

.merchant-login-form{
  margin: 0 auto;
  padding: .2rem .15rem;
  max-width:  640px;
  .form-group{
    margin-bottom: .1rem;
  }
  .form-control{
    @include input;
    box-shadow:0 0 0 rgba(0,0,0,0);
    -webkit-appearance:none;
    &[disabled] {
      color: #8e8e93;
    }
  }
  .form-captcha{
    display: flex;
    justify-content: space-between;
    .captcha{
      display: inline-block;
      width: 60%;
    }
    .getcaptcha{
      display: inline-block;
      width: 38%;
      &[disabled] {
        width: 38%;
      }
    }
  }
  .btn{
    @include m-btn;
    width: 100%;
    height: .4rem;
    font-size: .14rem;
    border: none;
  }
  .btn-default{
    font-size: .16rem;
    color: #8e8e93;
    background-color: #eee;
  }
  .btn.btn-active{
    color: #fff;
    border-color: #2692fb;
    background-color: #2692fb;
  }
  .btn[disabled]{
    @include m-btn;
    width: 100%;
    height: .4rem;
    font-size: .14rem;
    color: #8e8e93;
    background: #eee;
    border: none;
  }
}


