.h5-fs-detail {
    padding: 0 10px;
    width: 228px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 35px;
    left: 0;
    overflow: auto;
    &::-webkit-scrollbar {
        width: 4px;
        background: #ddd;
        border-radius: 2px;
    }
    &::-webkit-scrollbar-thumb {
        background: #78aadb;
        border-radius: 2px;
    }
}
.ant-tabs-bar {
    margin-bottom: 0;
}


