@import "../../Elements/base.scss";

$itemConH: 25px;
$default-height : 50px;
$col-margin-bottom:8px;

.item-con{
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  color: #333;
  .name{
    padding: 4px 0;
    width: 30px;
    text-align: right;
  }
  .item-img-box , .inputCommon{
    width: 132px;
  }
  .item-img-box{
    position: relative;
    .img-rule {
      display: block;
      margin-top: 2px;
      color: #999;
      cursor: pointer;
      &:hover {
        color: #ea4f48;
      }
    }
    .item-img-revoke{
      position: absolute;
      right: 0;
      bottom: 20px;
      padding: 1px 2px;
      color: #fff;
      font-size: 12px;
      background: rgba(0,0,0,0.3);
      cursor: pointer;
    }
    .icon-revoke{
      display: inline-block;
      margin-right: 2px;
      width: 14px;
      height: 14px;
      background:  url(../../../../../../images/icon-revoke.png) no-repeat;
      background-size: 100% 100%;
      vertical-align: middle;
    }
  }
  .inputCommon{
    @include input;
  }
}

.ele-col-2-img-r-preview {
  margin: 4px 3% 4px 0;
  width: 48.5%;
  height: $editor-preview-base-height * 0.75;
  font-size: 12px;
  background-size: auto 100%!important;
  // background-position-x: -80%!important;
  &:nth-child(2n){
      margin-right: 0;
  }
  .title{
    padding-top: $editor-preview-base-height / 2 - 16px;
    padding-left: 6px;
    width: 66px;
    @include text-overflow;
  }
}
.ele-col-3-img-r-preview {
  width  : 33.3%;
  height : $default-height;
  margin-bottom: $col-margin-bottom;
  .title {
    display       : block;
    padding-left  : 5px;
    font-size     : 12px;
    line-height   : $default-height;
    color         : #4c627e;
    width         : 55px;
    white-space   : nowrap;
    overflow      : hidden;
    text-overflow : ellipsis;
  }
}
.ele-col-4-img-r-preview{
  margin: 3px 0;
  width  : 25%;
  height: $editor-preview-base-height;
  font-size: 12px;
  text-align: center;
  background-size: $editor-preview-base-height - 22px!important;
  background-position-y: 3px!important;
  .title{
    padding-top: $editor-preview-base-height - 19px;
    width: 60px;
    -webkit-line-clamp:1;
    @include text-overflow;
  }
}
.ele-col-4-preview{
  $ele-col-4-preview-width : 91%;
  margin: 4px 3% 4px 0;
  width: $ele-col-4-preview-width / 4;
  height: $editor-preview-base-height;
  background-size:100% 100%!important;
  &:nth-child(4n){
    margin-right: 0 !important;
  }
}
@media screen and (max-height:600px){
  .ele-col-2-img-r-preview {
    $ele-col-2-img-r-preview-w: 97%;
    margin: 3px 3% 3px 0;
    width: 48.5%;
    height: $editor-preview-base-small-height * 0.75;
    font-size: 12px;
    background-size: auto 100%!important;
    .title{
      padding-top: 18px;
      width: 60px;
    }
  }
    .ele-col-4-preview{
      $ele-col-4-preview-w: 91%;
      margin-right: 3%;
      width: $ele-col-4-preview-w / 4;
      height: $editor-preview-base-small-height;
    }
    .ele-col-4-img-r-preview{
      height: $editor-preview-base-small-height;
      background-size: 42px 42px!important;
    .title{
      padding-top: 46px;
    }
  }
}


