.h5-fs-header{
  position: relative;
  background: #fff;
  height: 42px;
  font-size: 20px;
  line-height: 42px;
  color: #888;
  text-align: center;
  border-bottom: 1px solid #d9dbdf;
  z-index: 7;
  .title {
    font-size: 25px;
    text-align: center;
    cursor: pointer;
    margin: 0;
    img {
      width: 30px;
      height: 31px;
      vertical-align: middle;
      margin-right: 7px;
    }
    span {
      vertical-align: middle;
      display: inline-block;
    }
  }
  .logo {
    position: absolute;
    color: #2e93fb;
    left: 20px;
    vertical-align: middle;
    top: -2px;
  }
  .return-setting {
    position: absolute;
    top: 0;
    right: 150px;
    font-size: 16px;
    a {
      color: #666;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  .login {
    position: absolute;
    right: 20px;
    top: 0;
    font-size: 16px;
    color: #666;
    cursor: pointer;
    a {
      font-size: 16px;
      color: #666;
      vertical-align: middle;
    }
    a:hover {
      text-decoration: underline;
    }
    span {
      vertical-align: middle;
      color: #000;
      margin: 0 5px;
    }
  }
}




