@import "../../Elements/base.scss";

.preview-ele{
  .card-s2{
    padding: 4px 10px;
    // height: $editor-preview-base-height * 2.5;
    .title{
      padding-bottom: 6px;
      font-size: 14px;
      color: #333;
    }
    .card-img-box{
      padding: 0 10px;
      position: relative;
      .card-img,.img-shadow{
        margin-top: 4px;
        border-radius: 6px;
      }
      .card-logo {
        width: 32px;
        height: 32px;
        position: absolute;
        left: 130px;
        top: 50px;
      }
    }
    .img-shadow{
      height: 22px;
      background: linear-gradient(rgba(0,0,0,0.15), rgba(0,0,0,0));
    }
  }
}


