@import "~layouts/CoreLayout/CoreLayout";
@import "./sidebarLeft.scss";
@import "./sidebarRight.scss";
@import "./slider.scss";

@import "../../../H5Editor/Elements/base";

// $preview-header-height : 35px;

/*滚动条展示高度计算*/

.edit-shop-container {
  position   : absolute;
  top        : 42px;
  bottom     : 0;
  left       : 0;
  right      : 0;
  background : #f1f1f3;
  overflow   : scroll;
}

/**************************
 * new-mobile-editor-container
 **************************/
.editor-content {
  position   : absolute;
  z-index    : 5;
  left       : 220px;
  right      : 215px;
  top        : 50%;
  // margin-top: -$editor-c-preview-margin + 22px;
  // // padding    : $editor-content-padding 0;
  transform: translateY(-50%);
  .editor-preview-content{
    height   : $editor-c-preview-height - 80px;
    // overflow: scroll;
    overflow-x: hidden;
    &::-webkit-scrollbar{
      display: none;
    }
  }
    .scroll-wrap {
      position : absolute;
      left: 0;
      right: 0;
      z-index  : 1;
      padding: 4px;
      overflow : hidden;
    }
    .slider-wrap {
      position   : absolute;
      width      : 40px;
      height     : 300px;
      right      : -80px;
      top        : 50%;
      margin-top : -150px;
      // transform: translate3d(0, -50%, 0);
    }
    .slide-text {
      position             : absolute;
      z-index              : 5;
      top                  : 50%;
      right                : -40px;
      -webkit-writing-mode : vertical-rl;
      writing-mode         : vertical-rl;
      letter-spacing       : 5px;
      color                : #555;
      text-shadow          : 1px 1px 1px #999;
      margin-top           : -150px;
      display              : inline-block;
      width                : 20px;
      text-overflow        : ellipsis;
      overflow             : hidden;
      font-size            : 12px;
    }
    .mobile-header {
      position: relative;
      top: 0;
      left: 0;
      right: 0;
      z-index: 2;
      // height      : $preview-header-height;
      // line-height : $preview-header-height;
      text-align  : center;
      background  : #17beff;
      .title {
        font-size      : 12px;
        letter-spacing : 1px;
        color       : white;
      }
    
  }
  .text {
    margin-top : 10px;
    font-size  : 14px;
    color      : #888;
    text-align : center;
  }
}

// .header-hide {
//   display: none;
// }


@media (max-width : 640px) {
  .sidebar-left {
    width : 100px;
  }
  .h5-sidebar-right {
    display : none;
  }
  .editor-content {
    right : 0;
    left  : 100px;
  }
}

@media (max-height : 520px) {
  .pad_view {
    .sidebar-left {
      top: 0;
    }
    .h5-sidebar-right {
      top: 0;
    }
  }
}

.btn-block {
  position : absolute;
  bottom   : 20px;
  width    : 100%;
}

.h5-scroll-wrap {
  overflow-x: hidden !important;
  overflow-y: auto !important;
}



