@import '~normalize.css/normalize';
@import './base.scss';
@import url(http://nres.ffan.com/newh5/ee9c2a3c67f074b711351ae777522e49618a0ef6/iconfont/iconfont.css);

*,
*:before,
*:after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

// Some best-practice CSS that's useful for most apps
// Just remove them if they're not what you want
html {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  font-family: PingFang SC, "Helvetica Neue", Helvetica, 'Hiragino Sans GB', 'Microsoft YaHei', 'WenQuanYi Micro Hei', Arial,sans-serif;
  -webkit-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none;
}

button{
  font-family: PingFang SC, "Helvetica Neue", Helvetica, 'Hiragino Sans GB', 'Microsoft YaHei', 'WenQuanYi Micro Hei', Arial,sans-serif;
}

ul,ol,li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

a {
  color: #000;
  text-decoration:none;
}

a:active {
  color: #2692fb;
}

.clearfix{
  &:after {
    content:".";
    display:block;
    height:0;
    clear:both;
    visibility:hidden;
  }
}

.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
@mixin text-overflow{
  display: inline-block;
  word-break: break-all;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
@mixin text-overflow-all{
    display: inline-block;
    text-overflow: ellipsis;
	white-space: nowrap;
    word-break: break-all;
	display: -webkit-box;
    -webkit-line-clamp: 1;
}



