$menu-cont-height: 380px;

.sidebar-left {
  font-size: 12px;
  position: fixed;
  // top: 45px;
  bottom: 0;
  left: 0;
  width: 220px;
  // overflow-y: scroll;
  background: #00162b;

  $collapse-heignt: 38px;
  /*
  .menu-cont {
    &::-webkit-scrollbar {
      width:14px;
    }
    &::-webkit-scrollbar-button{
      display: none;
    }
    &::-webkit-scrollbar-track{
      background:#1a1e21;
    }
    &::-webkit-scrollbar-thumb{
      background:#8ca2bd;
      border-radius:6px;
    }
    &::-webkit-scrollbar-corner{
      background:#8ca2bd;
    }
  }
  */
  .menu-title {
    position: relative;
    padding: 0 10px;
    height: 40px;
    color: #fff;
    font-size: 14px;
    line-height: 40px;
    &:before{
      content: " ";
      position: absolute;
      bottom: 1px;
      left: 0;
      width: 100%;
      height: 1px;
      background: #141b24;
    }
    &:after{
      content: " ";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: #3e4a5b;
    }
    .fr-icon{
        float: right;
        padding-right: 14px;
        color: #d0cfcf;
    }
    .iconfont {
      margin-right: 0px;
      width: 20px;
      font-size: 16px;
      display: inline-block;
      vertical-align: middle;
    }

    word-wrap: break-word;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .btn-more {
    overflow: hidden;
    position: absolute;
    bottom: 0;
    height :$collapse-heignt;
    left: 0;
    right: 0;
    padding: 10px 7px;
    color: #748094;
    background: #2e363d;
    z-index: 2;
    .text {
      padding-left: 25px;
    }
    .iconfont {
      position: absolute;
      top: 8px;
    }
    .icon-puzzle {
      left: 10px;
    }
    .icon-readMore {
      right: 10px;
      // color: #f3f3f4;
    }
  }
}




