html {font-size: 100px;}
@media (min-width: 480px) {
    html {font-size: 110px !important;}}
@media (min-width: 640px) {
    html {font-size: 120px !important;}}
@media (min-width: 720px) {
    html {font-size: 130px !important;}}

.order-wrap{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #f3f4f6; 
  @mixin flex{
    display: flex;
    justify-content: space-between;
  }
  .status-done-color{
    color: #2dbc2f;
  }
  .status-prepaycount-color{
    color: #2692fb;
  }
  .status-done,.status-prepaycount{
    height: .36rem;
    line-height: .36rem;
    font-size: .14rem;
    text-align: center;
    background-color: #fff;
  }
  .status-done:before{
    content: " ";
    display: inline-block;
    margin-right: .04rem;
    width: .14rem;
    height: .14rem;
    background-image: url(http://timg.ffan.com/convert/resize/url_T123hTBXZg1RCvBVdK/tfs/1.png);
    background-size: 100% 100%;
    vertical-align: middle;
  }
  .status-prepaycount:before{
    content: " ";
    display: inline-block;
    margin-right: .04rem;
    width: .14rem;
    height: .14rem;
    background-image: url(http://timg.ffan.com/convert/resize/url_T1HBhTBKK_1RCvBVdK/tfs/1.png);
    background-size: 100% 100%;
    vertical-align: middle;
  }
  .title{
    padding: .12rem .1rem .08rem;
    font-size: .15rem;
    color: #000;
    background-color: #f3f4f6;
    border-top: 1px solid #ebebeb;
  }
  .store-details{
    @include flex;
    padding: .09rem .1rem .1rem;
    border-bottom: 1px solid #ebebeb;
    background-color: #fff;
    .img{
      width: .75rem;
      height: .75rem;
    }
    .store-price{
      @include flex;
      flex-direction: column;
      flex: 1;
      padding-left: .1rem;
      .name{
        font-size: .16rem;
        color: #000;
      }
      .price-num{
        @include flex;
        align-items: flex-end;
      }
      .price{
        font-size: .16rem;
        color: #2692fb;
        i{
          font-style: normal;
          font-size: .12rem;
        }
      }
      .num{
        font-size: .14rem;
        color: #000;
      }
    }
    
  }
  .store-pay-mode,.store-all-price{
    @include flex;
    margin: .05rem 0;
    padding: 0 .1rem;
    height: .44rem;
    line-height: .44rem;
    font-size: .16rem;
    color: #000;
    background-color: #fff;
    border-top: 1px solid #ebebeb;
    border-bottom: 1px solid #ebebeb;
    .icon-wechat-pay{
      padding-right: .04rem;
      color: #52a54a;
      font-size: .15rem;
    }
  }
  .store-total{
    padding: .14rem .1rem;
    text-align: right;
    font-size: .15rem;
    color: #000;
    span{
      color: #2692fb;;
    }
  }
  .store-roder-info{
    padding: .1rem;
    font-size: .13rem;
    color: #000;
    line-height: .24rem;
    background-color: #fff;
    border-top: 1px solid #ebebeb;
    border-bottom: 1px solid #ebebeb;
    span{
      display: inline-block;
      padding-left: .08rem;
      color: #999;
      line-height: .15rem;
    }
  }
  .pay-btn{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0; 
    height: .45rem;
    line-height: .45rem;
    font-size: .17rem;
    text-align: center;
    color: #fff;
    background-color: #2692fb;
  }
  .over-time-btn {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: .45rem;
    line-height: .45rem;
    font-size: .17rem;
    text-align: center;
    color: #fff;
    background-color: #ccc;
  }
}


